@import url(https://fonts.googleapis.com/css2?family=Saira:wght@100;300;600;800&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

section {
  box-sizing: border-box;
  background-color: #ffffff;
}

section:nth-child(even) {
  background-color: #fbfbfd;
}

html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  left: 0;
  top: 0;
  position: relative;
  font-size: 100%;
}

html {
  height: 100%;
  overflow: auto;
}
body {
  height: 100%;
}

/* ROOT FONT STYLES */

* {
  font-family: 'Saira', sans-serif;
  color: #333447;
  line-height: 1.5;
}

#dev-container {
  display: flex;
  justify-content: center;
  text-align: center;
  flex-direction: row;
  width: 100%;
  height: 60px;
}

#dev-anim {
  box-sizing: border-box;
  padding: 0 16px;
  position: relative;
  overflow: hidden;
  max-width: 400px;
}

#we-develop div {
  margin-left: 4px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-sizing: border-box;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 1.5rem;
  height: 60px;
}

#we-develop strong {
  font-weight: normal;
}

#career-cards {
  max-width: 750px;
  width: 100%;
  margin-bottom: 16px;
}

.job {
  display: flex;
  position: relative;
  flex-direction: column;
  justify-content: flex-end;
  padding: 0;
  height: 250px;
  border: 1px solid slateblue;
}

.job .content {
  padding: 16px;
  height: 80px;
  color: #fff;
}

.job p {
  padding: 16px;
}

.job a {
  position: absolute;
  right: 12px;
  top: 8px;
  font-size: 14px;
  font-weight: 600;
  color: #9a56b6;
}

#opn {
  -webkit-transition: scale 2s ease;
  transition: scale 2s ease;
  -webkit-animation-name: him;
          animation-name: him;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

@-webkit-keyframes him {
  from {
    scale: 1;
  }
  to {
    scale: 1.1;
  }
}

@keyframes him {
  from {
    scale: 1;
  }
  to {
    scale: 1.1;
  }
}

#home {
  min-height: 110vh;
  height: 100%;
  position: relative;
  background: rgb(141, 75, 168);
  background: linear-gradient(
    45deg,
    rgba(141, 75, 168, 1) 0%,
    rgba(154, 86, 182, 1) 35%,
    rgba(204, 135, 233, 1) 100%
  );
}

#contact {
  margin: 24px auto;
  padding: 24px;
  max-width: 400px;
  box-shadow: 0 8px 24px 24px rgba(0, 0, 0, 0.1);
  background-color: #fff;
}

#menu .navbar-menu {
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  justify-content: center;
}

#menu .navbar-menu.is-active {
  background-color: transparent;
}

#menu span {
  font-size: 1.15rem;
  color: #fff;
}

.navbar-item:hover {
  background-color: transparent !important;
  font-weight: 600;
}

#home .columns {
  height: 80vh;
  align-items: center;
}

#home {
  background-color: transparent;
}

.career {
  max-width: 800px;
  padding: 24px;
  margin: 0 auto;
}

.career ul {
  list-style: circle;
  margin-left: 48px;
}

.career li {
  padding-left: 24px;
  font-size: 1.2rem;
  line-height: unset;
  margin-bottom: 12px;
}

.career .title {
  font-weight: normal;
  margin-bottom: 32px !important;
  margin-top: 64px !important;
}

.career blockquote {
  padding-left: 16px;
  font-size: 16px;
  margin: 8px 16px 16px 16px;
  border-left: 4px solid #ccc;
}

.career strong {
  font-weight: 600;
}

.career .subtitle {
  line-height: unset;
}

