.career {
  max-width: 800px;
  padding: 24px;
  margin: 0 auto;
}

.career ul {
  list-style: circle;
  margin-left: 48px;
}

.career li {
  padding-left: 24px;
  font-size: 1.2rem;
  line-height: unset;
  margin-bottom: 12px;
}

.career .title {
  font-weight: normal;
  margin-bottom: 32px !important;
  margin-top: 64px !important;
}

.career blockquote {
  padding-left: 16px;
  font-size: 16px;
  margin: 8px 16px 16px 16px;
  border-left: 4px solid #ccc;
}

.career strong {
  font-weight: 600;
}

.career .subtitle {
  line-height: unset;
}

